@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap');

$breakpoint-mobile: 335px;
$breakpoint-tablet: 758px;
$breakpoint-desktop: 1024px;

html {
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background: #ffffff; }

  &::-webkit-scrollbar-thumb {
    border-radius: 3.5px;
    background-color: #ced4da;

    &:hover {
      background-color: #adb5bd; } }

  &::-webkit-scrollbar-track {
    background: #ffffff; } }

* {
  font-family: 'Noto Sans KR', sans-serif; }
h3 {
  font-size: 1.4em; }
a {
  &:link, &:visited {
    color: #121212; }
  &:hover, &:active, {
    color: #9e9e9e; } }
.container {
  display: block;
  width: 60%;
  margin: 0 auto; }

.contents {
  padding: 10px;
  & > div {
    vertical-align: top;
    ul {
      padding: 0 30px;
      font-weight: 400; } }
  .chapter {
    margin: 3px 0; }
  .chapter-container {
    text-align: center; }
  .imgDiv {
    display: inline-block;
    width: 50%;
    vertical-align: top;
    & > p {
      margin: 0;
      color: #969696; } }
  .imgSquare {
    width: 100%; }
  .text {
    font-size: 1.1em;
    text-align: left;
    display: inline-block;
    width: fit-content;
    margin: 0 16px;
    line-height: 2.2em; } }
.member-list {
  & > div {
    padding: 1%;
    display: inline-block;
    width: 18%;
    vertical-align: top;
    text-align: center;
    font-size: 1.3em;
    img {
      width: 100%;
      border-radius: 8px; }
    p {
      margin: 5px 0; } } }
@media (max-width: 810px) {
  .container {
    width: 100%; } }
.stack {
  * {
    vertical-align: top; }
  & > li > span {
    font-size: 1.6em;
    margin: 0 4px; } }

iframe {
  display: block;
  margin: 15px auto; }
